.portfolio__item {
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
}
.portfolio__img {
  height: 250px;
  width: 100%;
  object-fit: cover;
}
.portfolio__hover {
  background-color: var(--first-color);
  position: absolute;
  inset: 0;
  display: grid;
  place-items: center;
  opacity: 0;
  transition: all 0.5s ease;
}
.portfolio__item:hover .portfolio__hover {
  opacity: 1;
}
.portfolio__title {
  color: var(--text-white);
  font-size: var(--large-font-size);
  font-weight: var(-weight-500);
  transform: translateY(-20px);
  transition: all 0.5s ease;
}
.portfolio__item:hover .portfolio__title {
  transform: translateY(0);
}
.portfolio__modal {
  position: fixed;
  inset: 0;
  background-color: hsla(0, 0%, 0%, 0.7);
  z-index: 1000;
  display: grid;
  place-items: center;
  padding: 15px;
}
.portfolio__modal-content {
  /* top: 3.5%; */
  background-color: var(--container-color);
  /* max-width: 650px; */
  height: auto;
  /* padding: 32px; */
  border-radius: 10px;
  position: relative;
}
.modal__close {
  position: absolute;
  /* top: -3.5%;
  right: -8.5%; */
  width: 50px;
  height: 50px;
  filter: invert(0.8);
  padding: 10px;
}
.modal__title {
  text-align: center;
  color: var(--first-color);
  font-size: var(--h2-font-size);
  letter-spacing: 2px;
  margin-bottom: 24px;
}
.modal__img {
  width: 100%;
  /* height: 350px; */
  object-fit: cover;
}
.modal__list {
  /* grid-template-columns: repeat(2, 1fr); */
  row-gap: 10px !important;
  margin-bottom: 24px;
}
.modal__item {
  display: flex;
  align-items: center;
  column-gap: 8px;
  color: var(--text-color);
}
.item__icon {
  font-size: var(--small-font-size);
  display: flex;
}
.item__title,
.item__details {
  font-size: var(--small-font-size);
}

.item__details {
  font-weight: var(--weight-600);
}
